import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { API_URL } from '../constants/constants';
import { appConfig } from "./../services/config.service";
import { retry, catchError } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {

  constructor(private http: HttpClient) { }

  private headers = new HttpHeaders({ 'Content-Type': 'application/json' });

  getSystemInformation(id: any): Observable<any> {
    return this.http.get<any>(appConfig.API_URL + '/api/DialService/GetSystemInformation/' + id, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  } 

  getCustomer(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Customer/GetCustomer', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  getAllBreakTypes(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/BreakType/getAllBreakTypes', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  createBreakType(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/BreakType/CreateBreakType', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  UpdateBreakType(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/BreakType/UpdateBreakType/' + data.breakType.id, data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  deleteBreakType (data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/BreakType/DeleteBreakType/' + data.breakType.id, data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  getAllEmailTemplates(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/EmailTemplate/GetAllEmailTemplates', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  getAllUserStatusNotifications(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/UserStatusNotification/GetAllUserStatusNotifications', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  createUserStatusNotification(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/UserStatusNotification/CreateUserStatusNotification', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  UpdateUserStatusNotification(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/UserStatusNotification/UpdateUserStatusNotification/' + data.notification.userStatusID, data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  deleteUserStatusNotification (data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/UserStatusNotification/DeleteUserStatusNotification/' + data.notification.userStatusID, data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  authenticateServices(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Customer/AuthenticateServices', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  clearLines(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Customer/ClearLines', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  updateTimeZone(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/TimeZone/updateTimeZone/' + data.customerTimeZone.id, data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }
  
  updateCustomerSettings(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Customer/UpdateCustomerSettings', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  GetAllEmailTemplates(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/EmailTemplate/GetAllEmailTemplates', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  getAllRecordingTemplates(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/EmailTemplate/GetAllRecordingTemplates', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  createEmailTemplate(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/EmailTemplate/CreateEmailTemplate', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  updateEmailTemplate(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/EmailTemplate/UpdateEmailTemplate/' + data.emailTemplate.id, data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  deleteEmailTemplate(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/EmailTemplate/DeleteEmailTemplate/' + data.emailTemplate.id, data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  getFileNames(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/Customer/GetFileNames', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  testEmailTemplate(data: any): Observable<any> {
    return this.http.post<any>(appConfig.API_URL + '/api/EmailTemplate/TestEmailTemplate/' + data.emailTemplate.id, data,{ headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }
  
  handleError(error: any) {
    return throwError(error.message || error);
  }
}
