import { Component, OnInit, Inject } from '@angular/core';
import { CommonService } from 'src/app/services/common.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-delete',
  template: `
        <div class="dialog">
          <div class="dialog-header">
          <h4>Confirm</h4>
        </div>

        <div class="close" (click)="close('')">
          <span class="material-icons"> close </span>
        </div>
        <!-- <div class="close" (click)="close('')">
          <span class="material-icons">
            close
          </span>
        </div> -->

        <div class="dialog-contents">
          <h4 style="margin-bottom: 8px">{{this.data.text}}</h4>

          <br>
          <div class="confirm" *ngIf="!data.event">          
            <button mat-raised-button color="accent" (click)="close('Yes')">Yes, Delete</button>
            <button mat-raised-button (click)="close('No')">Cancel</button>
          </div>

          <div class="confirm" *ngIf="data.event === 'logout'">          
            <button mat-raised-button color="accent" (click)="close('Yes')">Logout</button>
          </div>

          <div class="confirm" *ngIf="data.event === 'login'">          
            <button mat-raised-button color="accent" (click)="close('Yes')">Try Again</button>
          </div>

          <div class="clearfix"></div>
        </div>
    </div>
  `,
  styles: [`
    .confirm{
      float: right
    }
    .confirm button{
      margin-left: 12px
    }
    .error{
      color: red
    }
    `]
})
export class DialogDeleteComponent implements OnInit {

  constructor(private CommonService: CommonService, public dialogRef: MatDialogRef<DialogDeleteComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,) { }

  ngOnInit(): void {
  }

  close(type: string): void {
    this.dialogRef.close(type);
  }

}
